import React from 'react';
import { connect } from 'react-redux';
import Profile from './Profile';
import { getProfileStatus, getUserProfile, updateProfileStatus } from '../../redux/profile-reducer';
import { Redirect, withRouter } from "react-router";
import withAuthRedirect from '../../hoc/withAuthRedirect';
import { compose } from 'redux';

class ProfileContainer extends React.Component {

  componentDidMount() {
    let userID = this.props.match.params.userID;
    if (!userID) {
      userID = this.props.authorizedUserID;
      if (!userID) {
        this.props.history.push('/login')
      }
    }
    this.props.getUserProfile(userID)
    this.props.getProfileStatus(userID)
  }

  render() {
    return (
      <Profile {...this.props} profile={this.props.profile} status={this.props.status}
        updateProfileStatus={this.props.updateProfileStatus} />
    )
  }
}

let mapStateToProps = (state) => {
  return {
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    authorizedUserID: state.auth.userID
  }
}


export default compose(connect(mapStateToProps, { getUserProfile, getProfileStatus, updateProfileStatus }), withRouter)(ProfileContainer)